<template>
    <div class="layout-px-spacing">  
        <ul class="navbar-nav flex-row mt-2">
            <li>
                <div class="page-header">
                    <nav class="breadcrumb-one" aria-label="breadcrumb">
                        <ol class="breadcrumb">
                            <li class="breadcrumb-item"><a href="javascript:;">Good Received Notes</a></li>
                            <li class="breadcrumb-item active" aria-current="page"><span> Pending Good Received Note Items </span></li>
                        </ol>
                    </nav>
                </div>
            </li>
        </ul>

        <h3 class="text-center">Pending Good Received Note Items</h3>

        <div class="d-flex justify-content-between align-items-center mb-3">
            <globalSearchField
                v-model:filterValue="filter"
            />
            <button @click="printPendingGrnItemsReport()" class="btn btn-success ms-2">
                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-printer" viewBox="0 0 16 16">
                    <path d="M2.5 8a.5.5 0 1 0 0-1 .5.5 0 0 0 0 1z"/>
                    <path d="M5 1a2 2 0 0 0-2 2v2H2a2 2 0 0 0-2 2v3a2 2 0 0 0 2 2h1v1a2 2 0 0 0 2 2h6a2 2 0 0 0 2-2v-1h1a2 2 0 0 0 2-2V7a2 2 0 0 0-2-2h-1V3a2 2 0 0 0-2-2H5zM4 3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1v2H4V3zm1 5a2 2 0 0 0-2 2v1H2a1 1 0 0 1-1-1V7a1 1 0 0 1 1-1h12a1 1 0 0 1 1 1v3a1 1 0 0 1-1 1h-1v-1a2 2 0 0 0-2-2H5zm7 2v3a1 1 0 0 1-1 1H5a1 1 0 0 1-1-1v-3a1 1 0 0 1 1-1h6a1 1 0 0 1 1 1z"/>
                </svg>
            </button>
        </div>
        
              <!-- loading spinner starts -->
        <div v-if="loading_spinner" v-html="tableShimmer()"></div>
        <!-- Loading spinner ends -->
        <div v-else class="row layout-top-spacing">
            <div class="col-xl-12 col-lg-12 col-sm-12 layout-spacing">
                <div class="panel p-0">
                    <div class="custom-table table3">
                        <v-client-table :data="filteredData" :columns="columns" :options="table_option">
                            <template #actions="props">
                
                                <router-link :to="`/approval-chain/${props.row.approval_list_id}`" class="text-primary">
                                    <i class="fas fa-check-circle"></i>
                                    <svg
                                        xmlns="http://www.w3.org/2000/svg"
                                        width="24"
                                        height="24"
                                        viewBox="0 0 24 24"
                                        fill="none"
                                        stroke="currentColor"
                                        stroke-width="2"
                                        stroke-linecap="round"
                                        stroke-linejoin="round"
                                        class="text-primary"
                                    >
                                        <path d="M1 12s4-8 11-8 11 8 11 8-4 8-11 8-11-8-11-8z"></path>
                                        <circle cx="12" cy="12" r="3"></circle>
                                    </svg>
                                </router-link>
                                
                            </template>
                        </v-client-table>
                    </div>
                </div>
            </div>
        </div>

    </div>
</template>
<script setup>
    import { onMounted, ref, inject, computed } from 'vue';
    import axios from 'axios';
    import { useMeta } from '@/composables/use-meta';
    import '@suadelabs/vue3-multiselect/dist/vue3-multiselect.css';
    import globalSearchField from '@/components/reusables/globalSearchField.vue';
    import { globalSearch } from '@/components/reusables/globalSearch';
    
    useMeta({ title: 'Pending Good Received Note Items' });  
    
    const showError = inject('showError');
    const formatNumber = inject('formatNumber');
    const tableShimmer = inject('tableShimmer');
    const printPendingGrnItemsReport = inject('printPendingGrnItemsReport');    
    
    const loading_spinner = ref(false);
    const columns = ref(['SNo', 'grn_no','lpo_no','project', 'requester', 'vendor', 'item', 'units', 'rate', 'quantity','cost','actions']);    
    const grns = ref([]);
    const filter = ref('')

    const filteredData = computed(() => {
      return globalSearch(grns.value, filter.value);
    });

    const table_option = ref({
        perPage: 20,
        perPageValues: [20, 30, 50, 100],
        skin: 'table table-hover table-striped',
        columnsClasses: { actions: 'actions text-center' },
        sortable: ['SNo', 'grn_no','lpo_no','project','requester','vendor','item','units','rate','quantity','cost','actions'],
        sortIcon: {
            base: 'sort-icon-none',
            up: 'sort-icon-asc',
            down: 'sort-icon-desc',
        },
        pagination: { nav: 'scroll', chunk: 5 },
        texts: {
            count: 'Showing {from} to {to} of {count}',
            filter: '',
            filterPlaceholder: 'Search...',
            limit: 'Results:',
        },
        resizableColumns: false,
    });

    const axiosInstance = axios.create({
        baseURL: process.env.VUE_APP_API_BASE_URL
    });

    const getGrnItems = async () => {
        loading_spinner.value = true;
        try {
            const response = await axiosInstance.get('pending-grn-items');
            grns.value = response.data.map((item, index) => ({
                ...item,
                SNo: index + 1,
                grn_no: item.grn_id, 
                lpo_no: item.approval_list_id, 
                project: item.requisition_items?.requisition_type?.requisition?.project?.short_name ?? 'no project',
                requester: item.requester,
                vendor: item.requisition_items?.vendor?.name ?? 'no vendor',                
                purpose: item.approval_list?.approval_group,
                item: item.requisition_items?.remark,
                units: item.requisition_items?.units,
                rate: formatNumber(item.requisition_items?.rate),                
                quantity: item.quantity,
                cost: formatNumber(item.quantity * item.requisition_items?.rate),
                description: item.requisition_items?.description,
            }));
        } catch (error) {
          console.log(error)
            showError(error.message);
        }finally{
            loading_spinner.value = false;
        }
    };

    onMounted(() => {
        getGrnItems();
    });
</script>